export const Banner = () => {
    const wu1s = [
        {
            title: "100 % Confidentiel", icon: 'mdi-lock-pattern',
            text: 'A small river named Duden flows by their place and supplies it with the necessary regelialia.'
        },
        {
            title: "Formateur(s) qualifi(s)", icon: 'mdi-teach',
            text: 'A small river named Duden flows by their place and supplies it with the necessary regelialia.'
        },
        {
            title: "Approche individuelle", icon: 'mdi-seat',
            text: 'A small river named Duden flows by their place and supplies it with the necessary regelialia.'
        }
    ]
    return <div className='hBanner'>
        <section className='hb-text'>
            <Fade>
                <h1>{contactInfo.nom} {contactInfo.prenom} </h1>
            </Fade>
            <Fade bottom>
                <p> Pour ce qui est de lavenir,
                    il ne sagit pas de le prvoir mais de le rendre possible.
                    <br />
                    <b>- Antoine de Saint-Exupry</b>
                </p>
            </Fade>
            <div>
                <Reveal cascade bottom>
                    <Link to="">Nous contactez</Link>
                    <Link to="">En savoir plus</Link>
                </Reveal>
            </div>

        </section>

        <section className='whyUs1'>
            <Fade cascade bottom>
                {
                    wu1s.map((wu1, index) => <div key={"why us 1 nb" + index}>
                        <span>
                            <i className={'mdi ' + wu1.icon}></i>
                        </span>
                        <div>
                            <b>{wu1.title} </b>
                            <p>
                                A small river named Duden flows by their place and supplies it with the necessary regelialia.
                            </p>
                        </div>
                    </div>)
                }
            </Fade>
        </section>

    </div>
}

const VideoShow = ({ shower }) => {
    return <div className="videoPreview">
        <span className="vpCloser" onClick={event => shower(false)}>
            <i className="mdi mdi-close"></i>
        </span>
        <section>
            {/* require('../Assets/images/video.mp4') */}
            <ReactPlayer url={""}
                controls={true} />
        </section>
    </div>
}

export const LowPresentation = () => {
    const [showVideo, setSV] = useState(false);

    return <div className="lowPresentation">
        <Fade>
            <section className="lp-video">
                <SimpleImage props={{ src: "coachPics/3.jpg", alt: "video lol", className: "" }} />
                <button onClick={event => setSV(true)}>
                    <i className="mdi mdi-play"></i>
                </button>

            </section>
        </Fade>

        <section className="lp-text">
            <Reveal bottom cascade>
                <b>Bienvenue sur DidaFir Coaching</b>
                <h3>Un deuxieme titre</h3>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                    sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                    Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip
                    ex ea commodo consequat.
                    {/* Almost before we knew it, we had left the ground  */}
                </p>
                <Link to="">Decouvrir nos tarif</Link>
            </Reveal>
        </section>
        {
            showVideo ? <VideoShow shower={setSV} /> : ''
        }
    </div>
}

