export const OurPrestations = () => {
    const ourPrestations = [
        {
            icon: 'mdi-heart', title: 'Relation social',
            text: " Utilisateurs bnficient de notre accompagnement",
            img: 'relation.jpg'
        },
        {
            icon: 'mdi-heart', title: 'Couple',
            text: " En moyenne perus Annuellement par ces derniers.",
            img: 'couple.jpg'
        },
        {
            icon: 'mdi-heart', title: 'Dpression',
            text: " Utilisateurs bnficient de notre accompagnement",
            img: 'depression.jpg'
        },
        {
            icon: 'mdi-heart', title: 'Relation Familialle',
            text: " En moyenne perus Annuellement par ces derniers.",
            img: 'famille.jpg'
        }, {
            icon: 'mdi-heart', title: 'Problme personnel',
            text: " Utilisateurs bnficient de notre accompagnement",
            img: 'personnel.jpg'
        },
        {
            icon: 'mdi-heart', title: 'Problme de buisness',
            text: " En moyenne perus Annuellement par ces derniers.",
            img: 'buisness.jpg'
        }
    ]
    const [hopResult, setHopResult] = useState(0)
    function handleHopChange(id) {
        setHopResult(id)
    }

    return <div className='h-ourPrestations'>
        <Fade>
            <h2>Nos domaine de prestations </h2>
        </Fade>

        <section>
            <div className='hop-selecter'>
                <Reveal bottom cascade>
                    {
                        ourPrestations.map((op, index) => <label key={"our prestation nb" + index}
                            className={index === hopResult ? "hopSelected" : ""}>
                            <b>{op.title} </b>
                            <input type="radio" name="hop" id={"hop-" + (index + 1)}
                                onClick={(event) => handleHopChange(index)}
                            />
                        </label>)
                    }
                </Reveal>
            </div>

            <div className='hop-result'>
                <SimpleImage props={{
                    src: "prestations/" + ourPrestations[hopResult].img,
                    alt: "hop image", className: ""
                }} />
                <b>{ourPrestations[hopResult].title} </b>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                    sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                    Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
                    aliquip ex ea commodo consequat.
                    Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum
                </p>
            </div>
        </section>
    </div>
}







